.tip_cover {
  position: fixed;
  top: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  -webkit-tap-highlight-color: transparent;
}
.tip_cover .tip_w {
    position: absolute;
    top: 48%;
    left: 0;
    transition: opacity 0.5s;
    -webkit-transition: opacity 0.5s;
    width: 100%;
    text-align: center;
}
.tip_cover .tip_w .cont {
      display: inline-block;
      background: rgba(0, 0, 0, 0.7);
      max-width: 50%;
      padding: 20px 40px;
      /*no*/
      border-radius: 10px;
      /*no*/
      font-size: 28px;
      /*no*/
      color: #fff;
      text-align: center;
}
.tip_cover .tip_w.hide {
      opacity: 0;
}

